*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.skills-page{
    background-image: linear-gradient(to bottom, #051937, #0e162c, #121322, #111119, #0e0e0e);
}
.skills-card{
    box-shadow: 0px 0px 15px 2px cyan;
    width: 15%;
}
@media(max-width:500px){
    .skills-card{
        width: 30%;
        height: 100%;
    }
}